@charset "UTF-8";
/* Slider */
.slick-loading .slick-list {
  background: #fff url("./ajax-loader.gif") center center no-repeat; }

/* Icons */
@font-face {
  font-family: "slick";
  src: url("./fonts/slick.eot");
  src: url("./fonts/slick.eot?#iefix") format("embedded-opentype"), url("./fonts/slick.woff") format("woff"), url("./fonts/slick.ttf") format("truetype"), url("./fonts/slick.svg#slick") format("svg");
  font-weight: normal;
  font-style: normal; }

/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 60px;
  width: 60px;
  line-height: 60px;
  color: #fff;
  background-color: #191B1F;
  border-radius: 50%;
  cursor: pointer;
  text-align: center;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
  z-index: 10;
  transition: opacity .3s; }
  @media screen and (max-width: 768px) {
    .slick-prev,
    .slick-next {
      height: 50px;
      width: 50px;
      line-height: 50px; } }
  .slick-prev:hover, .slick-prev:focus,
  .slick-next:hover,
  .slick-next:focus {
    outline: none;
    opacity: 0.6; }
  .slick-prev.slick-disabled:before,
  .slick-next.slick-disabled:before {
    opacity: 0.25; }
  .slick-prev:before,
  .slick-next:before {
    font-size: 22px;
    line-height: 60px;
    font-weight: 600; }
    @media screen and (max-width: 768px) {
      .slick-prev:before,
      .slick-next:before {
        line-height: 50px; } }

.slick-prev {
  left: 15px; }
  [dir="rtl"] .slick-prev {
    left: auto;
    right: -25px; }
  .slick-prev:before {
    content: "←"; }
    [dir="rtl"] .slick-prev:before {
      content: "→"; }

.slick-next {
  right: 15px; }
  [dir="rtl"] .slick-next {
    left: -25px;
    right: auto; }
  .slick-next:before {
    content: "→"; }
    [dir="rtl"] .slick-next:before {
      content: "←"; }

/* Dots */
.slick-dotted.slick-slider {
  margin-bottom: 30px; }

.slick-dots {
  position: absolute;
  bottom: -25px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%; }
  .slick-dots li {
    position: relative;
    display: inline-block;
    height: 3px;
    width: 60px;
    margin: 0 5px;
    padding: 0;
    cursor: pointer; }
    @media screen and (max-width: 768px) {
      .slick-dots li {
        width: 40px; } }
    .slick-dots li button {
      border: 0;
      background: transparent;
      display: block;
      height: 3px;
      width: 60px;
      outline: none;
      line-height: 0px;
      font-size: 0px;
      color: transparent;
      cursor: pointer;
      background-color: #F1F1F1; }
      @media screen and (max-width: 768px) {
        .slick-dots li button {
          width: 40px; } }
      .slick-dots li button:hover, .slick-dots li button:focus {
        outline: none; }
        .slick-dots li button:hover:before, .slick-dots li button:focus:before {
          opacity: 1; }
      .slick-dots li button:before {
        position: absolute;
        top: 0;
        left: 0;
        content: "";
        width: 60px;
        height: 3px;
        font-family: "slick";
        font-size: 6px;
        text-align: center;
        color: black;
        opacity: 0.25;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale; }
        @media screen and (max-width: 768px) {
          .slick-dots li button:before {
            width: 40px; } }
    .slick-dots li.slick-active button:before {
      color: black;
      opacity: 0.75;
      background-color: #0e63c6; }
